ntPage{font-weight:200;font-size:1.2em;background-color:#f0f0f0;color:#2a373d;padding:5px 9px;border-radius:2px;margin:0 3px}.table-container-pagination-btnContainer-btn{background-color:#f0f0f0;border:none}.arrow-previous,.arrow-next{width:11px;fill:#000;background-color:none;margin-top:3px}.arrow-previous:hover,.arrow-next:hover{fill:#c7ced1}.arrow-next{transform:rotate(90deg)}.arrow-previous{transform:rotate(270deg)}.table{width:100%;position:relative;cursor:pointer}.table td,.table th{opacity:85%;height:35px;width:100%;font-size:.9em;text-align:center;justify-content:center}.table thead svg{width:7px;padding-left:6px;margin-bottom:-3}.selectedStyle{background-color:#7f6be2;color:#fff}.sort-svg-light{fill:#000}.sort-svg-dark{fill:#fff}.sort-svg-light:hover{fill:#5c6366;transform:scale(1.1);cursor:pointer}.sort-svg-dark:hover{fill:#f8f8f8;transform:scale(1.1);cursor:pointer}.table-row-light:nth-of-type(odd){background:#f8f8f8bb}.table-row-light:hover{background-color:#f0f0f0}.table-row-dark:nth-of-type(odd){background:#5c6366;color:#fff}.table-row-dark:hover{background-color:#5c6366;color:#fff}@media only screen and (min-width: 1023px){.table td,.table th{font-size:1em}}
